<template>
  <div class="container">
    <div class="row">
      <div class="col">
        <h3>Game Stats:</h3>
      </div>
    </div>
    <div class="row">
      <div class="col" v-if="startTime">Start Time: {{computedStartTime}}</div>
    </div>
    <div class="row">
      <div class="col" v-if="startTime">End Time: {{computedEndTime}}</div>
    </div>
    <div class="row" v-if="startTime">
      <div class="col">Time Left: {{computeTimeLeft}}</div>
    </div>
    <div class="row">
      <div class="col">Reward: {{currentReward}} ETH</div>
    </div>
    <div class="row">
      <div class="col">Highest Total Bid: {{highestTotalBid}} ETH</div>
    </div>
    <div class="row">
      <div class="col">Number Player: {{numberPlayer}}</div>
    </div>
    <div class="row">
      <div class="col">Last Bid: {{lastBid}} ETH</div>
    </div>
    <div class="row">
      <div class="col">Next Min Bid: {{nextMinBid}} ETH</div>
    </div>
    <div class="row">
      <div class="col">Total Amount Reward : {{totalAmountReward}} ETH</div>
    </div>
    <div class="row">
      <div class="col">
        <h3>Your Stats:</h3>
      </div>
    </div>
    <div class="row">
      <div class="col">Current Round Total Bid: {{myRoundTotalBid}} ETH</div>
    </div>
    <div class="row">
      <div class="col">Total Time Played: {{myTotalTimePlayed}}</div>
    </div>
    <div class="row">
      <div class="col">Total Amount Played: {{myTotalAmountPlayed}} ETH</div>
    </div>
    <div class="row">
      <div class="col">Total Amount Reward: {{myTotalAmountReward}} ETH</div>
    </div>
    <div class="row">
      <div class="col">
        <input v-model="userBid" placeholder="bid" />
      </div>
    </div>
    <div class="row">
      <div class="col" v-if="!loading">
        <button @click="play" v-if="computeTimeLeft !== 0">{{startTime ? "Bid" : "New Round"}}</button>
        <button @click="gameCheck" v-if="computeTimeLeft === 0">Reveal Winner</button>
      </div>
      <div class="col" v-if="loading">
        <div class="load-1">
          <div class="line"></div>
          <div class="line"></div>
          <div class="line"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import web3 from "../contract/web3";
import bidContract from "../contract/bidContractInstance";
import Swal from "sweetalert2";
import dayjs from "dayjs";
import * as utils from "../utils";

const decimal = 10 ** 18;

export default {
  name: "User",
  components: {},
  data: function () {
    return {
      timeLeft: -1,
      gameSeconds: 0,
      startTime: 0,
      endTime: 0,
      lastBid: -1,
      stepBid: -1,
      userBid: 0,
      highestTotalBid: 0,
      currentReward: 0,
      myTotalPlayed: 0,
      myTotalAmountPlayed: 0,
      myTotalAmountReward: 0,
      myTotalTimePlayed: 0,
      myRoundTotalBid: 0,
      numberPlayer: 0,
      totalAmountReward: 0,
      loading: false,
      computeTimeLeft: 0,
      nextMinBid: 0
    };
  },
  computed: {
    computedStartTime: function () {
      return dayjs(this.startTime * 1000).format("DD-MM-YYYY hh:mm:ss");
    },
    computedEndTime: function () {
      return dayjs(this.endTime*1000).format(
        "DD-MM-YYYY hh:mm:ss"
      );
    },
  },
  created: function () {
    var eventCreateBlocks = new Set();
    var eventWinBlocks = new Set();
    var eventNewBidBlocks = new Set();

    bidContract.events.Start({ fromBlock: "latest" }, (error, event) => {
      if (error) return console.log(error);
      const blockNumber = event.blockNumber;
      //accept only new events
      if (eventCreateBlocks.has(blockNumber)) return;
      eventCreateBlocks.add(blockNumber);

      console.log("start event: ", event);

      // this.createTime = parseInt(event.returnValues.createTime);
      // this.startTime = parseInt(event.returnValues.startTime);

      this.getGameStats();
      this.getMyStats();

      this.$toast.open({
        type: "info",
        duration: 20000,
        position: "top-right",
        dismissible: true,
        message: `Game number ${event.returnValues.totalGames} started`,
      });
    });

    bidContract.events.Bid({ fromBlock: "latest" }, (error, event) => {
      if (error) return console.log(error);
      const blockNumber = event.blockNumber;
      //accept only new events
      if (eventNewBidBlocks.has(blockNumber)) return;
      eventNewBidBlocks.add(blockNumber);

      console.log("Bid event: ", event);

      // this.lastBid = parseFloat(event.returnValues.bid) / decimal;

      this.getGameStats();
      this.getMyStats();

      this.$toast.open({
        type: "info",
        duration: 20000,
        position: "top-right",
        dismissible: true,
        message: `Player ${utils.shortenAddress(event.returnValues.creator)} bid ${
          utils.sunToEth(event.returnValues.bid)
        } ETH`,
      });
    });

    bidContract.events.Win({ fromBlock: "latest" }, (error, event) => {
      if (error) return console.log(error);
      const blockNumber = event.blockNumber;
      //accept only new events
      if (eventWinBlocks.has(blockNumber)) return;
      eventWinBlocks.add(blockNumber);

      console.log("Win event: ", event);

      // this.createTime = 0;
      // this.startTime = 0;

      this.getGameStats();
      this.getMyStats();

      this.$toast.open({
        type: "info",
        position: "top-right",
        duration: 20000,
        dismissible: true,
        message: `Player ${utils.shortenAddress(event.returnValues.creator)} win round ${event.returnValues.gameNumber}, reward ${
          utils.sunToEth(event.returnValues.amount)
        }`,
      });
    });

    this.getGameStats();
    this.getMyStats();

    this.tick = setInterval(() => {
      if (this.endTime > 0) {
        const seconds = this.endTime - Math.floor(Date.now() / 1000);
        this.computeTimeLeft = seconds > 0 ? seconds : 0;
      } else {
        this.computeTimeLeft = -1;
      }
    }, 1000);
  },
  destroyed: function () {
    clearInterval(this.tick);
  },
  methods: {
    async play() {
      try {
        if (this.userBid < this.nextMinBid)
          return alert("need large than min bid");

        this.loading = true;
        const accounts = await web3.eth.getAccounts();

        bidContract.methods
          .play()
          .send({
            from: accounts[0],
            value: this.userBid * decimal,
          })
          .on("transactionHash", () => {
            this.loading = true;
          })
          .on("receipt", (receipt) => {
            if (receipt.status) {
              Swal.fire(
                "Information",
                "Bid " + this.userBid + "ETH",
                "success"
              );
            } else {
              Swal.fire("Information", "Transaction failed", "error");
            }
            this.loading = false;
          })
          .on("error", () => {
            Swal.fire("Information", "Transaction failed", "error");
            this.loading = false;
          });
      } catch (e) {
        console.log(e);
      }
    },
    async gameCheck() {
      this.loading = true;
      const accounts = await web3.eth.getAccounts();

      bidContract.methods
        .gameCheck()
        .send({
          from: accounts[0],
        })
        .on("transactionHash", () => {
          this.loading = true;
        })
        .on("receipt", (receipt) => {
          if (receipt.status) {
            Swal.fire("Information", "Reveal Winner", "success");
          } else {
            Swal.fire("Information", "Transaction failed", "error");
          }
          this.loading = false;
        })
        .on("error", () => {
          Swal.fire("Information", "Transaction failed", "error");
          this.loading = false;
        });
    },
    async getGameStats() {
      const gameSettings = await bidContract.methods.gameSettings().call();

      this.lastBid = parseFloat(utils.sunToEth(gameSettings.lastBid));
      this.stepBid = parseFloat(gameSettings.stepBid);
      this.startTime = parseInt(gameSettings.startTime);
      this.endTime = parseInt(gameSettings.endTime);
      this.gameSeconds = parseInt(gameSettings.gameTimerSeconds);

      this.nextMinBid = parseFloat(utils.sunToEth(await bidContract.methods.getNextMinBid().call()));

      this.numberPlayer = parseInt(
        await bidContract.methods.getPlayersLength().call()
      );
      this.currentReward = utils.sunToEth(await bidContract.methods.getReward().call())
 

      const highestRoundPlayer = await bidContract.methods
        .highestRoundPlayer()
        .call();
      this.highestTotalBid = utils.sunToEth(highestRoundPlayer.totalBid);
    },
    async getMyStats() {
      const accounts = await web3.eth.getAccounts();
      const myStats = await bidContract.methods.myStats(accounts[0]).call();

      this.myRoundTotalBid = utils.sunToEth(myStats.currentRoundTotalBid);
      this.myTotalTimePlayed = parseInt(myStats.totalTimePlayed);
      this.myTotalAmountPlayed = utils.sunToEth(myStats.totalAmountPlayed);
      this.myTotalAmountReward = utils.sunToEth(myStats.totalAmountReward);
    },
  },
};
</script>

<style scoped lang="scss">
.line {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 15px;
  background-color: #4b9cdb;
}

.load-1 .line:nth-last-child(1) {
  animation: loadingA 1.5s 1s infinite;
}
.load-1 .line:nth-last-child(2) {
  animation: loadingA 1.5s 0.5s infinite;
}
.load-1 .line:nth-last-child(3) {
  animation: loadingA 1.5s 0s infinite;
}

@keyframes loadingA {
  0% {
    height: 15px;
  }
  50% {
    height: 35px;
  }
  100% {
    height: 15px;
  }
}
</style>